// imported Modules =============================================
import { React, useState, useEffect } from "react";

import axios from "axios";

import styled from "styled-components";
import { Input, Button, Divider, message } from "antd";
import { LeftOutlined } from "@ant-design/icons";

// imported components ==========================================
import Loading from "../../../../ShareComponents/atoms/Loading";

import CenterPicker from "./CenterPicker";

// Main Component ===============================================
import DataStorage from "../../../../DataStorage";
const SEND_CHECK_MAIL = DataStorage("SEND_CHECK_MAIL");
const ROUTE_MANAGER = DataStorage("ROUTE_MANAGER");
const CREATE_NEWLAB_MOVEIN = DataStorage("CREATE_NEWLAB_MOVEIN");

const FORMDATA_INIT_VALUE = {
  labName: null,
  lat: null,
  lng: null,
  zoom: null,
  customIcon: false,
  // defaultPermission: null,
  routePw: "",
  captainEmail: null,
  // captainPw: null,
};

const IS_VALIDATED_INIT_VALUE = {
  all: false,
  labName: false,
  captainEmail: false,
};

export default function Movein({ setCurrentContent, contentsIndex, setLabCreatorVisible }) {
  const [formData, setFormData] = useState(FORMDATA_INIT_VALUE);
  const [isValidated, setIsValidated] = useState(IS_VALIDATED_INIT_VALUE);
  const [btnLoading, setBtnLoading] = useState(IS_VALIDATED_INIT_VALUE);

  const [isLoading, setIsLoading] = useState(false);

  const [centerPickerVisible, setCenterPickerVisible] = useState(false);
  const [currentCenter, setCurrentCenter] = useState(null);

  useEffect(() => {
    if (currentCenter) {
      setFormData({
        ...formData,
        lat: currentCenter.lat,
        lng: currentCenter.lng,
        zoom: currentCenter.zoom,
      });
    }
  }, [currentCenter]);

  function onChangeForm(target, value) {
    setFormData({ ...formData, [target]: value });
  }

  async function validate(e) {
    // target Ï†ïÌïòÍ∏∞
    let target;
    if (e.target.name) {
      target = e.target.name;
    } else {
      // Î≤ÑÌäº ÎÇ¥Î∂ÄÏùò Í∏ÄÏî®Í∞Ä ÏÑ†ÌÉùÎêú Í≤ΩÏö∞, Î∂ÄÎ™®Ïùò nameÎ∞õÏïÑÏò§Í∏∞
      target = e.target.parentNode.name;
    }

    // targetÎ≥Ñ Í≤ÄÏ¶ùÌïòÍ∏∞ : all, captainEmail, labName
    if (target === "all") {
      for (let key of Object.keys(formData)) {
        if (key === "routePw" || key === "customIcon") continue;

        if (formData[key] === null || formData[key] === "") {
          message.warn("ÎπÑÏñ¥ÏûàÎäî Í≥≥ÏùÑ Ï±ÑÏõåÏ£ºÏÑ∏Ïöî");
          setIsValidated({ ...isValidated, all: false });
          return;
        }
      }

      for (let key of Object.keys(isValidated)) {
        if (key !== "all" && isValidated[key] === false) {
          message.warn("ÎåÄÏû•Ïù¥Î©îÏùº ÎòêÎäî Ïó∞Íµ¨ÏÜåÏù¥Î¶ÑÏù¥ ÏÇ¨Ïö© Í∞ÄÎä•ÌïúÏßÄ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî");
          setIsValidated({ ...isValidated, all: false });
          return;
        }
      }
      setIsValidated({ ...isValidated, all: true });
    } else if (target === "captainEmail") {
      setBtnLoading({ ...btnLoading, captainEmail: true });
      const { data } = await axios.post(
        SEND_CHECK_MAIL,
        JSON.stringify({ captainEmail: formData.captainEmail })
      );
      if (data.status === "success") {
        setIsValidated({ ...isValidated, captainEmail: true });
        message.info("ÌôïÏù∏Î©îÏùºÏùÑ Î≥¥ÎÇ¥ÎìúÎ†∏Ïñ¥Ïöî. Î©îÏùºÌï®ÏùÑ Íº≠ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî!");
      } else {
        message.error("Î¨∏Ï†úÍ∞Ä ÏÉùÍ≤® Î©îÏùºÏùÑ Î≥¥ÎÇ¥ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
        console.error(data);
      }
      setBtnLoading({ ...btnLoading, captainEmail: false });
    } else if (target === "labName") {
      setBtnLoading({ ...btnLoading, labName: true });

      if (await isAvailableLabName(formData.labName)) {
        setIsValidated({ ...isValidated, labName: true });
        message.success("ÏÇ¨Ïö© Í∞ÄÎä•Ìïú Ïó∞Íµ¨ÏÜå Ïù¥Î¶ÑÏûÖÎãàÎã§");
      } else {
        message.error("ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÎäî Ïó∞Íµ¨ÏÜå Ïù¥Î¶ÑÏûÖÎãàÎã§");
      }
      setBtnLoading({ ...btnLoading, labName: false });
    } else {
      console.error("ÏòàÏÉÅÌïòÏßÄ Î™ªÌïú Í≤ÄÏ¶ùÏöîÏ≤≠");
    }
  }

  function pickCenter() {
    setCurrentCenter({
      lat: formData.lat,
      lng: formData.lng,
      zoom: formData.zoom,
    });
    setCenterPickerVisible(true);
  }

  async function onSubmit() {
    setIsLoading(true);

    const target = { target: { name: "all" } };
    await validate(target);

    if (isValidated.all) {
      message.success("ÏÉà Ïó∞Íµ¨ÏÜåÎ•º Ïó¥Ïã¨Ìûà Ï§ÄÎπÑÌïòÍ≥†ÏûàÏñ¥Ïöî!", 0);

      const {
        data: { data, status },
      } = await axios.post(CREATE_NEWLAB_MOVEIN, JSON.stringify(formData));
      message.destroy();
      setIsLoading(false);

      if (status === "success") {
        setCurrentContent(contentsIndex.selectType);
        setLabCreatorVisible(false);
        message.success("ÏÉàÎ°úÏö¥ Ïó∞Íµ¨ÏÜåÍ∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÉùÏÑ±ÎêòÏóàÏäµÎãàÎã§");
        console.log(data);
      } else {
        message.error("Î¨¥Ïñ∏Í∞Ä ÏûòÎ™ªÎêòÏóàÏäµÎãàÎã§");
        console.error(data);
      }
    } else {
      setIsLoading(false);
    }
  }

  return (
    <MoveinWrap className="Movein">
      <Button
        onClick={(e) => setCurrentContent(contentsIndex.selectType)}
        shape="circle"
        icon={<LeftOutlined />}
      ></Button>
      <ContentWrap>
        <h2>üè¢ ÏûÖÏ£º</h2>
        <p>Î™á Í∞ÄÏßÄ ÏÇ¨Ï†ÑÏ†ïÎ≥¥Î•º ÏïåÎ†§Ï£ºÏãúÎ©¥ Î∞îÎ°ú ÏûÖÏ£ºÌïòÏã§ Ïàò ÏûàÎèÑÎ°ù ÎèÑÏôÄÎìúÎ¶¥Í≤åÏöî üëã</p>

        <Divider>ÎåÄÏû• Ï†ïÎ≥¥</Divider>
        <MultiInput>
          <StyledInput
            placeholder="ÎåÄÏû•ÎãòÏùò Email"
            name="captainEmail"
            value={formData.captainEmail}
            onChange={(e) => onChangeForm(e.target.name, e.target.value)}
            disabled={btnLoading.captainEmail || isValidated.captainEmail}
          />

          {isValidated.captainEmail ? (
            <Button
              name="captainEmail"
              onClick={(e) => setIsValidated({ ...isValidated, captainEmail: false })}
            >
              ÏàòÏ†ï
            </Button>
          ) : (
            <Button name="captainEmail" loading={btnLoading.captainEmail} onClick={validate}>
              ÌôïÏù∏
            </Button>
          )}
        </MultiInput>

        {/* <StyledPwInput placeholder="Ïó∞Íµ¨ÏÜå Í¥ÄÎ¶¨ ÎπÑÎ∞ÄÎ≤àÌò∏" name="captainPw" value={formData.captainPw} onChange={e => onChangeForm(e.target.name, e.target.value)} /> */}

        <Divider>Ïó∞Íµ¨ÏÜå Ï†ïÎ≥¥</Divider>
        <MultiInput>
          <StyledInput
            placeholder="Ïó∞Íµ¨ÏÜå Ïù¥Î¶Ñ"
            name="labName"
            value={formData.labName}
            onChange={(e) => onChangeForm(e.target.name, e.target.value)}
            disabled={btnLoading.labName || isValidated.labName}
          />
          {isValidated.labName ? (
            <Button
              name="labName"
              onClick={(e) => setIsValidated({ ...isValidated, labName: false })}
            >
              ÏàòÏ†ï
            </Button>
          ) : (
            <Button name="labName" loading={btnLoading.labName} onClick={validate}>
              ÌôïÏù∏
            </Button>
          )}
        </MultiInput>

        <StyledPwInput
          placeholder="(ÏÑ†ÌÉù) Ïó∞Íµ¨ÏÜå Ï∂úÏûÖ ÎπÑÎ∞ÄÎ≤àÌò∏"
          name="routePw"
          value={formData.routePw}
          onChange={(e) => onChangeForm(e.target.name, e.target.value)}
        />
        <MultiInput>
          <StyledInput disabled placeholder="Ïó∞Íµ¨ÏÜå ÏúÑÎèÑ" name="lat" value={formData.lat} />
          <StyledInput disabled placeholder="Ïó∞Íµ¨ÏÜå Í≤ΩÎèÑ" name="lng" value={formData.lng} />
          <StyledInput disabled placeholder="ÏßÄÎèÑ Í∏∞Î≥∏Î∞∞Ïú®" name="zoom" value={formData.zoom} />
          <Button onClick={pickCenter}>ÏàòÏ†ï</Button>
        </MultiInput>

        {/* <Select
          placeholder="Ïó∞Íµ¨ÏÜå Í∏∞Î≥∏ Í∂åÌïú"
          name="defaultPermission"
          value={formData.defaultPermission}
          onChange={(value) => onChangeForm("defaultPermission", value)}
        >
          <Select.Option value={"viewer"} key={0}>
            Ïó¥Îûå Í∞ÄÎä•
          </Select.Option>
          <Select.Option value={"editor"} key={1}>
            Ìé∏Ïßë Í∞ÄÎä•
          </Select.Option>
        </Select> */}

        <Divider />
        <Button onClick={onSubmit}>ÏûÖÏ£ºÌïòÍ∏∞</Button>
      </ContentWrap>

      {centerPickerVisible ? (
        <CenterPicker
          centerPickerVisible={centerPickerVisible}
          setCenterPickerVisible={setCenterPickerVisible}
          currentCenter={currentCenter}
          setCurrentCenter={setCurrentCenter}
        />
      ) : null}

      <Loading isLoading={isLoading} />
    </MoveinWrap>
  );
}

// style ========================================================
const MoveinWrap = styled.div``;

const ContentWrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  height: 100%;
`;

const MultiInput = styled.div`
  display: flex;

  & > input {
    margin-right: 4px;
  }
  & > .no-margin {
    margin-right: 0;
  }
`;

const StyledInput = styled(Input)`
  margin-bottom: 4px;
`;
const StyledPwInput = styled(Input.Password)`
  margin-bottom: 4px;
`;

// function =====================================================
async function isAvailableLabName(labName) {
  let isAvailable = true;

  if (labName === "" || labName === null) {
    isAvailable = false;
  } else if (containsOnlySpace(labName)) {
    isAvailable = false;
  } else {
    const { data } = await axios.get(`${ROUTE_MANAGER}?labName=${labName}`); // Ïó∞Íµ¨ÏÜå Ïù¥Î¶ÑÏù¥ Ï§ëÎ≥µÎêòÎäîÏßÄ Ïó¨Î∂Ä
    console.log(data);
    const isUnique = data.data;
    if (!isUnique) {
      isAvailable = false;
    }
  }

  return isAvailable;
}

function containsOnlySpace(str) {
  let res = true;

  for (let i = 0; i < str.length; i++) {
    if (str !== " ") {
      res = false;
      break;
    }
  }

  return res;
}
